<template>
  <div class="main-page">
    <div class="headerTag">修改密码</div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="info.newPass"
        type="password"
        name="新密码"
        label="新密码"
        placeholder="请输入新密码"
        input-align="right"
        required
        :rules="[{ required: true, message: '请填写新密码' }]"
      />

      <van-field
        v-model="info.confirmPass"
        type="password"
        name="确定密码"
        label="确定密码"
        placeholder="请确定新密码"
        input-align="right"
        required
        :rules="[{ required: true, message: '请确定新密码' }]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >提交</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  name: "My",
  props: {},
  components: {},
  data() {
    return {
      info: {
        newPass: null,
        confirmPass: null,
      },
      rules: {
        newPass: "请输入新密码 ！",
        confirmPass: "请再次输入新密码 ！",
      },
    };
  },
  methods: {
    async onSubmit() {
      // console.log(values);
      if (this.info.newPass !== this.info.confirmPass) {
        this.$u.msg("两次新密码不一致！");
        return;
      }
      this.$u
        .confirm("请确认修改密码！")
        .then(() => {
          return this._api
            .changePassWord({
              passWord: md5(
                this.info.newPass + "6002E411B72B694B5B76CD62EFFDA56E"
              ).toUpperCase(),
              UserName: this.myInfo.Fname,
            })
            .then((res) => {
              console.log(res);
              if (res) {
                this.$u.msg("密码修改成功！请重新登录！");
                setTimeout(() => {
                  this.$router.push("/Login");
                }, 500);
              }
            });
        })
        .catch(() => {});
    },
  },
};
</script>

<style lang="scss" scoped>
.main-page {
}
</style>
